import { OtelDiagInfo } from '@aspecto/opentelemetry-diag';

export const printLiveStreamMessage = ({ baseUrl, otelDiagInfo }: { baseUrl: string; otelDiagInfo: OtelDiagInfo }) => {
    const message = `🕵️‍♀️ See the \x1b[1mlive stream tracing\x1b[0m at ${baseUrl}/live-stream-traces?instanceid=${otelDiagInfo.instanceId}`;
    const msgLen = message.length - 10;
    const line = '='.repeat(msgLen + 2);
    const spacer = ' '.repeat(msgLen);
    console.log('');
    console.log(`${line}`);
    console.log(`|${spacer}|`);
    console.log(`|`, message, '|');
    console.log(`|${spacer}|`);
    console.log(`${line}`);
    console.log('');
};
